import React, { useState, useEffect, useCallback, useMemo, memo } from 'react';
import { useAuth } from "../../common/context/AuthContext";
import { useNavigate } from 'react-router-dom';
// import AdminNavbar removed - handled by ConditionalNavbar in App.js
import {
  UnifiedBackground,
  UnifiedCard,
  UnifiedButton,
  useScrollAnimation,
  SharedStyles
} from "../../common/components/SharedTheme";
import {
  DollarSign,
  Clock,
  CheckCircle,
  XCircle,
  Search,
  Filter,
  Eye,
  AlertCircle,
  Calendar,
  User,
  Phone,
  Mail,
  History,
  ChevronLeft,
  ChevronRight
} from 'lucide-react';
import {
  SkeletonWithdrawalCard,
  SkeletonPageHeader,
  SkeletonSearchFilters,
  SkeletonFullPage
} from '../components/skeletons';
import { supabase } from "../../common/services/supabaseClient"
import { useToast } from '../services/toastService';

// Memoized status badge component
const StatusBadge = memo(({ status }) => {
  const statusConfig = useMemo(() => ({
    pending: { color: 'bg-yellow-500', icon: Clock, text: 'Pending' },
    approved: { color: 'bg-blue-500', icon: CheckCircle, text: 'Approved' },
    completed: { color: 'bg-green-500', icon: CheckCircle, text: 'Completed' },
    rejected: { color: 'bg-red-500', icon: XCircle, text: 'Rejected' }
  }), []);

  const config = statusConfig[status] || statusConfig.pending;
  const IconComponent = config.icon;

  return (
    <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium text-white ${config.color}`}>
      <IconComponent className="w-3 h-3 mr-1" />
      {config.text}
    </span>
  );
});

const getDateTimeParts = (timestamp) => {
  if (!timestamp) return { date: '—', time: '' };
  const dateObj = new Date(timestamp);
  return {
    date: dateObj.toLocaleDateString('en-IN', { dateStyle: 'medium' }),
    time: dateObj.toLocaleTimeString('en-IN', { timeStyle: 'short' })
  };
};

const formatDateTime = (timestamp) => {
  const { date, time } = getDateTimeParts(timestamp);
  if (!time) return date;
  return `${date} ${time}`;
};

function AdminWithdrawals() {
  const { user, logout } = useAuth();
  const navigate = useNavigate();
  const { showSuccess, showError, showWarning, handleApiError } = useToast();
  useScrollAnimation();

  // State management
  const [withdrawalRequests, setWithdrawalRequests] = useState([]);
  const [statusFilter, setStatusFilter] = useState('all');
  const [loading, setLoading] = useState(true);
  const [actionNotes, setActionNotes] = useState('');
  const [transactionId, setTransactionId] = useState('');
  const [rejectionReason, setRejectionReason] = useState('');

  // Search functionality
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredRequests, setFilteredRequests] = useState([]);

  // Modal management
  const [selectedRequest, setSelectedRequest] = useState(null);
  const [showModal, setShowModal] = useState(false);

  // Pagination states
  const [currentPage, setCurrentPage] = useState(1);
  const [entriesPerPage, setEntriesPerPage] = useState(10);

  // Load withdrawal requests from Supabase
  useEffect(() => {
    loadWithdrawalRequests();
  }, []);

  // Filter and search logic
  useEffect(() => {
    let filtered = withdrawalRequests || [];

    // Search filter
    if (searchTerm) {
      filtered = filtered.filter(request => {
        const name = (request?.profiles?.name || '').toLowerCase();
        const id = (request?.id || '').toString().toLowerCase();
        const searchLower = (searchTerm || '').toLowerCase();
        return name.includes(searchLower) || id.includes(searchLower);
      });
    }

    // Status filter
    if (statusFilter !== 'all') {
      filtered = filtered.filter(request =>
        (request?.status || 'pending') === statusFilter
      );
    }

    setFilteredRequests(filtered);
    setCurrentPage(1); // Reset to first page when filters change
  }, [withdrawalRequests, searchTerm, statusFilter]);

  const loadWithdrawalRequests = async () => {
    try {
      setLoading(true);

      // Load essential withdrawal data first - minimal fields for instant display
      const { data, error } = await supabase
        .from('withdrawal_requests')
        .select(`
          id,
          amount,
          status,
          created_at,
          profiles!withdrawal_requests_promoter_id_fkey(
            name
          )
        `)
        .order('created_at', { ascending: false })
        .limit(10); // Ultra-reduced for instant load

      if (error) {
        setWithdrawalRequests([]);
      } else {
        setWithdrawalRequests(data || []);
      }

      setLoading(false); // Stop loading immediately

      // Load more withdrawal data in background
      setTimeout(async () => {
        try {
          const { data: moreData } = await supabase
            .from('withdrawal_requests')
            .select(`
              id,
              amount,
              status,
              created_at,
              promoter_id,
              profiles!withdrawal_requests_promoter_id_fkey(
                name,
                promoter_id
              )
            `)
            .order('created_at', { ascending: false })
            .limit(50);

          if (moreData) {
            setWithdrawalRequests(moreData);
          }
        } catch (error) {
          console.error('Error loading more withdrawal data:', error);
        }
      }, 200);

      // Load full withdrawal data last
      setTimeout(async () => {
        try {
          const { data: fullData } = await supabase
            .from('withdrawal_requests')
            .select(`
              *,
              profiles!withdrawal_requests_promoter_id_fkey(
                name,
                email,
                phone,
                promoter_id
              )
            `)
            .order('created_at', { ascending: false })
            .limit(500);

          if (fullData) {
            setWithdrawalRequests(fullData);
          }
        } catch (error) {
          console.error('Error loading full withdrawal data:', error);
        }
      }, 800);

    } catch (error) {
      setWithdrawalRequests([]);
      setLoading(false);
    }
  };

  // Apply status filter to the search results
  const finalFilteredRequests = useMemo(() => {
    let filtered = filteredRequests;
    
    if (statusFilter !== 'all') {
      filtered = filtered.filter(request => request.status === statusFilter);
    }
    
    // Apply pagination
    const indexOfLastRequest = currentPage * entriesPerPage;
    const indexOfFirstRequest = indexOfLastRequest - entriesPerPage;
    return filtered.slice(indexOfFirstRequest, indexOfLastRequest);
  }, [filteredRequests, statusFilter, currentPage, entriesPerPage]);

  // Calculate summary statistics
  const totalRequests = withdrawalRequests.length;
  const pendingRequests = withdrawalRequests.filter(r => r.status === 'pending').length;
  const approvedRequests = withdrawalRequests.filter(r => r.status === 'approved').length;
  const completedAmount = withdrawalRequests
    .filter(r => r.status === 'completed')
    .reduce((sum, r) => sum + (r.amount || 0), 0);
  const rejectedRequests = withdrawalRequests.filter(r => r.status === 'rejected').length;

  // Handle approve/reject/complete actions
  const handleAction = async (requestId, action) => {
    try {
      let updateData = {
        status: action,
        processed_at: new Date().toISOString(),
        processed_by: user.id
      };

      if (action === 'approved' && actionNotes) {
        updateData.admin_notes = actionNotes;
      } else if (action === 'rejected' && rejectionReason) {
        updateData.rejection_reason = rejectionReason;
        updateData.admin_notes = actionNotes;
      } else if (action === 'completed' && transactionId) {
        updateData.transaction_id = transactionId;
        updateData.completed_at = new Date().toISOString();
        updateData.admin_notes = actionNotes;
      }


      const { data, error } = await supabase
        .from('withdrawal_requests')
        .update(updateData)
        .eq('id', requestId)
        .select();

      if (error) {
        alert('Failed to update withdrawal request: ' + error.message);
        return;
      }
      // Notification system removed - withdrawal status updated successfully

      alert(`Withdrawal request ${action} successfully!`);

      // Reset form and reload data
      closeActionModal();
      loadWithdrawalRequests();
    } catch (error) {
      alert('An error occurred while updating the request.');
    }
  };

  // Additional modal state
  const [actionType, setActionType] = useState('');

  const openActionModal = useCallback((request, action) => {
    setSelectedRequest(request);
    setActionType(action);
    setShowModal(true);
  }, []);

  const viewDetails = useCallback((request) => {
    setSelectedRequest(request);
    setShowModal(true);
  }, []);

  const closeActionModal = useCallback(() => {
    setShowModal(false);
    setActionNotes('');
    setTransactionId('');
    setRejectionReason('');
    setSelectedRequest(null);
  }, []);

  const closeDetailsModal = useCallback(() => {
    setShowModal(false);
    setSelectedRequest(null);
  }, []);


  if (loading) {
    return (
      <>
        <SharedStyles />
        {/* AdminNavbar removed - handled by ConditionalNavbar in App.js */}
        <SkeletonFullPage type="cards" />
      </>
    );
  }

  return (
    <>
      <SharedStyles />
      {/* AdminNavbar removed - handled by ConditionalNavbar in App.js */}
      <UnifiedBackground>
        <div className="min-h-screen pt-40 px-8 pb-8">
          <div className="container mx-auto px-4">
            {/* Header */}
            <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-6">
              <div>
                <h1 className="text-2xl font-bold text-white">Withdrawal Management</h1>
                <p className="text-slate-400">Process withdrawal requests and manage approval workflows</p>
              </div>
            </div>

            {/* Search and Filters */}
            <UnifiedCard className="mb-6" data-animate style={{ overflow: 'visible' }}>
              {/* Single Row Layout - All elements in one line */}
              <div className="flex flex-wrap items-center gap-3">
                {/* Search Input - Takes more space */}
                <div className="flex-1 min-w-[300px]">
                  <div className="relative">
                    <input
                      type="text"
                      placeholder="Search by promoter name or request ID..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                      className="w-full h-10 px-3 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all text-sm"
                    />
                    {/* Search Icon */}
                    <div className="absolute right-3 top-1/2 transform -translate-y-1/2 pointer-events-none">
                      <svg className="w-4 h-4 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                      </svg>
                    </div>
                  </div>
                </div>

                {/* Status Filter */}
                <div className="relative">
                  <select
                    value={statusFilter}
                    onChange={(e) => setStatusFilter(e.target.value)}
                    className="appearance-none h-10 bg-gray-700 border border-gray-600 rounded-lg text-white px-3 pr-8 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all cursor-pointer hover:bg-gray-650 text-sm"
                  >
                    <option value="all">All Status</option>
                    <option value="pending">Pending</option>
                    <option value="approved">Approved</option>
                    <option value="completed">Completed</option>
                    <option value="rejected">Rejected</option>
                  </select>
                  {/* Custom Dropdown Arrow */}
                  <div className="absolute right-3 top-1/2 transform -translate-y-1/2 pointer-events-none">
                    <svg className="w-3 h-3 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                    </svg>
                  </div>
                </div>

                {/* Pagination Controls */}
                <div className="flex items-center gap-2 h-10 px-3 bg-gray-700/30 rounded-lg border border-gray-600/50">
                  <span className="text-gray-300 text-xs font-medium whitespace-nowrap">Show</span>
                  <div className="relative">
                    <input
                      type="number"
                      min="1"
                      max="100"
                      value={entriesPerPage}
                      onChange={(e) => {
                        const value = Math.min(100, Math.max(1, parseInt(e.target.value) || 10));
                        setEntriesPerPage(value);
                        setCurrentPage(1); // Reset to first page when changing entries per page
                      }}
                      className="w-12 h-6 bg-gray-600 border border-gray-500 rounded text-white text-xs pl-2 pr-1 py-1 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 hover:bg-gray-500 transition-all text-center"
                      placeholder="10"
                    />
                  </div>
                  <span className="text-gray-300 text-xs font-medium whitespace-nowrap">withdrawals per page</span>
                </div>

                {/* Status Badge - Now in the same line with consistent styling */}
                {filteredRequests.length > 0 && (
                  <div className="flex items-center gap-2 h-10 px-3 bg-gray-700/30 rounded-lg border border-gray-600/50">
                    <div className="w-2 h-2 bg-blue-400 rounded-full animate-pulse"></div>
                    <span className="text-gray-300 text-sm font-medium">
                      Showing <span className="text-blue-400 font-bold">{((currentPage - 1) * entriesPerPage) + 1}</span> to <span className="text-blue-400 font-bold">{Math.min(currentPage * entriesPerPage, filteredRequests.length)}</span> of <span className="text-blue-400 font-bold">{filteredRequests.length}</span> withdrawals
                    </span>
                  </div>
                )}
              </div>
            </UnifiedCard>

            {/* Withdrawal Requests Table */}
            <UnifiedCard className="overflow-hidden" data-animate>
              {finalFilteredRequests.length === 0 ? (
                <div className="p-12 text-center">
                  <AlertCircle className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                  <h3 className="text-lg font-medium text-white mb-2">No Withdrawal Requests</h3>
                  <p className="text-gray-400">
                    {withdrawalRequests.length === 0
                      ? "No withdrawal requests have been submitted yet."
                      : "No requests match your current filters."
                    }
                  </p>
                </div>
              ) : (
                <div className="overflow-x-auto">
                  <table className="w-full">
                    <thead className="bg-gray-700">
                      <tr>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Request ID</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Promoter Details</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Amount</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Status</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Request Date</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Actions</th>
                      </tr>
                    </thead>
                    <tbody className="bg-gray-800 divide-y divide-gray-700">
                      {finalFilteredRequests.map((request) => (
                        <tr key={request.id} className="hover:bg-gray-700">
                          <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-white">
                            <p className="font-semibold">{request.request_number || 'N/A'}</p>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                            <div>
                              <p className="text-white font-medium">{request.profiles?.name || 'Unknown'}</p>
                              <p className="text-xs text-gray-400">{request.profiles?.promoter_id || 'No ID'}</p>
                            </div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-white font-semibold">
                            ₹{(request.amount || 0).toLocaleString()}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <StatusBadge status={request.status} />
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                            {(() => {
                              const { date, time } = getDateTimeParts(request.created_at || Date.now());
                              return (
                                <div className="flex flex-col leading-tight">
                                  <span className="text-white font-medium">
                                    {date}
                                  </span>
                                  <span className="text-xs text-gray-400">
                                    {time || '—'}
                                  </span>
                                </div>
                              );
                            })()}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                            <div className="flex space-x-2">
                              <button
                                onClick={() => viewDetails(request)}
                                className="text-blue-400 hover:text-blue-300 transition-colors"
                              >
                                <Eye className="w-4 h-4" />
                              </button>
                              {request.status === 'pending' && (
                                <>
                                  <button
                                    onClick={() => openActionModal(request, 'approved')}
                                    className="text-green-400 hover:text-green-300 transition-colors"
                                    title="Approve Request"
                                  >
                                    <CheckCircle className="w-4 h-4" />
                                  </button>
                                  <button
                                    onClick={() => openActionModal(request, 'rejected')}
                                    className="text-red-400 hover:text-red-300 transition-colors"
                                    title="Reject Request"
                                  >
                                    <XCircle className="w-4 h-4" />
                                  </button>
                                </>
                              )}
                              {request.status === 'approved' && (
                                <button
                                  onClick={() => openActionModal(request, 'completed')}
                                  className="text-blue-400 hover:text-blue-300 transition-colors"
                                  title="Mark as Completed"
                                >
                                  <DollarSign className="w-4 h-4" />
                                </button>
                              )}
                            </div>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              )}
            </UnifiedCard>
            
              {/* Bottom Pagination Controls */}
              {filteredRequests.length > 0 && (
                <UnifiedCard className="px-6 py-4 border-t border-gray-700/50 bg-gray-800/30" data-animate>
                  <div className="flex flex-col sm:flex-row justify-between items-center gap-4">
                    <div className="flex items-center gap-2 px-3 py-1.5 bg-gray-700/50 rounded-lg border border-gray-600/50">
                      <div className="w-2 h-2 bg-blue-400 rounded-full animate-pulse"></div>
                      <span className="text-gray-300 text-sm font-medium">
                        Page <span className="text-blue-400 font-bold">{currentPage}</span> of <span className="text-blue-400 font-bold">{Math.ceil(filteredRequests.length / entriesPerPage)}</span>
                      </span>
                    </div>
                    
                    <div className="flex items-center gap-2">
                      <button
                        onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                        disabled={currentPage === 1}
                        className={`group relative p-2.5 rounded-lg transition-all duration-200 ${
                          currentPage === 1 
                            ? 'bg-gray-700/50 text-gray-500 cursor-not-allowed border border-gray-600/30' 
                            : 'bg-gray-700 text-gray-300 hover:bg-gray-600 hover:text-white border border-gray-600/50 hover:border-gray-500 hover:shadow-lg hover:shadow-blue-400/10'
                        }`}
                        title="Previous Page"
                      >
                        <ChevronLeft className="w-4 h-4" />
                        <span className="absolute -bottom-8 left-1/2 transform -translate-x-1/2 text-xs text-gray-400 opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap">Previous</span>
                      </button>
                      
                      <div className="flex items-center gap-1 px-2 py-1 bg-gray-700/30 rounded-lg border border-gray-600/30">
                        {Array.from({ length: Math.min(5, Math.ceil(filteredRequests.length / entriesPerPage)) }, (_, i) => {
                          const totalPages = Math.ceil(filteredRequests.length / entriesPerPage);
                          let pageNumber;
                          
                          if (totalPages <= 5) {
                            pageNumber = i + 1;
                          } else if (currentPage <= 3) {
                            pageNumber = i + 1;
                          } else if (currentPage >= totalPages - 2) {
                            pageNumber = totalPages - 4 + i;
                          } else {
                            pageNumber = currentPage - 2 + i;
                          }
                          
                          return (
                            <button
                              key={pageNumber}
                              onClick={() => setCurrentPage(pageNumber)}
                              className={`w-8 h-8 rounded-md text-sm font-medium transition-all duration-200 ${
                                currentPage === pageNumber
                                  ? 'bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-lg shadow-blue-400/20 border border-blue-400/50'
                                  : 'bg-gray-600/50 text-gray-300 hover:bg-gray-600 hover:text-white border border-gray-500/30 hover:border-gray-400/50'
                              }`}
                              title={`Go to page ${pageNumber}`}
                            >
                              {pageNumber}
                            </button>
                          );
                        })}
                      </div>
                      
                      <button
                        onClick={() => setCurrentPage(prev => Math.min(prev + 1, Math.ceil(filteredRequests.length / entriesPerPage)))}
                        disabled={currentPage === Math.ceil(filteredRequests.length / entriesPerPage)}
                        className={`group relative p-2.5 rounded-lg transition-all duration-200 ${
                          currentPage === Math.ceil(filteredRequests.length / entriesPerPage)
                            ? 'bg-gray-700/50 text-gray-500 cursor-not-allowed border border-gray-600/30'
                            : 'bg-gray-700 text-gray-300 hover:bg-gray-600 hover:text-white border border-gray-600/50 hover:border-gray-500 hover:shadow-lg hover:shadow-blue-400/10'
                        }`}
                        title="Next Page"
                      >
                        <ChevronRight className="w-4 h-4" />
                        <span className="absolute -bottom-8 left-1/2 transform -translate-x-1/2 text-xs text-gray-400 opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap">Next</span>
                      </button>
                    </div>
                  </div>
                </UnifiedCard>
              )}
          </div>
        </div>

        {/* Action Modal */}
        {showModal && selectedRequest && actionType && (
          <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
            <UnifiedCard className="w-full max-w-md">
              <div className="p-6">
                <div className="flex items-center justify-between mb-6">
                  <h3 className="text-xl font-semibold text-white">
                    {actionType === 'approved' && 'Approve Withdrawal Request'}
                    {actionType === 'rejected' && 'Reject Withdrawal Request'}
                    {actionType === 'completed' && 'Mark as Completed'}
                  </h3>
                  <button
                    onClick={closeActionModal}
                    className="text-gray-400 hover:text-white transition-colors"
                  >
                    <XCircle className="w-5 h-5" />
                  </button>
                </div>

                <div className="space-y-4 mb-6">
                  <div className="bg-gray-800/50 p-4 rounded-lg">
                    <p className="text-gray-400 text-sm">Request Details</p>
                    <p className="text-white font-medium">Amount: ₹{selectedRequest.amount?.toLocaleString()}</p>
                    <p className="text-gray-300 text-sm">Promoter: {selectedRequest.profiles?.name}</p>
                    <p className="text-gray-300 text-sm">Request ID: {selectedRequest.request_number || selectedRequest.id}</p>
                  </div>

                  {actionType === 'rejected' && (
                    <div>
                      <label className="block text-gray-400 text-sm mb-2">Rejection Reason *</label>
                      <textarea
                        value={rejectionReason}
                        onChange={(e) => setRejectionReason(e.target.value)}
                        className="w-full px-4 py-2 bg-gray-800/50 border border-gray-700/50 rounded-lg text-white focus:outline-none focus:border-red-500/50"
                        placeholder="Enter reason for rejection..."
                        rows="3"
                        required
                      />
                    </div>
                  )}

                  {actionType === 'completed' && (
                    <div>
                      <label className="block text-gray-400 text-sm mb-2">Transaction ID *</label>
                      <input
                        type="text"
                        value={transactionId}
                        onChange={(e) => setTransactionId(e.target.value)}
                        className="w-full px-4 py-2 bg-gray-800/50 border border-gray-700/50 rounded-lg text-white focus:outline-none focus:border-green-500/50"
                        placeholder="Enter transaction/reference ID..."
                        required
                      />
                    </div>
                  )}

                  <div>
                    <label className="block text-gray-400 text-sm mb-2">Admin Notes (Optional)</label>
                    <textarea
                      value={actionNotes}
                      onChange={(e) => setActionNotes(e.target.value)}
                      className="w-full px-4 py-2 bg-gray-800/50 border border-gray-700/50 rounded-lg text-white focus:outline-none focus:border-blue-500/50"
                      placeholder="Add any additional notes..."
                      rows="2"
                    />
                  </div>
                </div>

                <div className="flex justify-end space-x-3">
                  <UnifiedButton
                    variant="secondary"
                    onClick={closeActionModal}
                  >
                    Cancel
                  </UnifiedButton>
                  <UnifiedButton
                    onClick={() => handleAction(selectedRequest.id, actionType)}
                    disabled={actionType === 'rejected' && !rejectionReason || actionType === 'completed' && !transactionId}
                    className={`${actionType === 'approved' ? 'bg-green-600 hover:bg-green-700' :
                        actionType === 'rejected' ? 'bg-red-600 hover:bg-red-700' :
                          'bg-blue-600 hover:bg-blue-700'
                      } disabled:opacity-50 disabled:cursor-not-allowed`}
                  >
                    {actionType === 'approved' && 'Approve Request'}
                    {actionType === 'rejected' && 'Reject Request'}
                    {actionType === 'completed' && 'Mark Completed'}
                  </UnifiedButton>
                </div>
              </div>
            </UnifiedCard>
          </div>
        )}

        {/* Details Modal */}
        {showModal && selectedRequest && !actionType && (
          <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
            <UnifiedCard className="w-full max-w-2xl">
              <div className="p-6">
                <div className="flex items-center justify-between mb-6">
                  <h3 className="text-xl font-semibold text-white">Withdrawal Request Details</h3>
                  <button
                    onClick={closeDetailsModal}
                    className="text-gray-400 hover:text-white transition-colors"
                  >
                    <XCircle className="w-5 h-5" />
                  </button>
                </div>

                <div className="space-y-6">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-gray-400 text-sm">Request ID</p>
                      <p className="text-white font-mono">{selectedRequest.id}</p>
                    </div>
                    <div>
                      <p className="text-gray-400 text-sm">Status</p>
                      <div className="mt-1"><StatusBadge status={selectedRequest.status} /></div>
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-gray-400 text-sm">Amount</p>
                      <p className="text-2xl font-bold text-white">₹{selectedRequest.amount?.toLocaleString()}</p>
                    </div>
                    <div>
                      <p className="text-gray-400 text-sm">Promoter</p>
                      <p className="text-white">{selectedRequest.profiles?.name}</p>
                      <p className="text-gray-300 text-sm">{selectedRequest.profiles?.email}</p>
                    </div>
                  </div>

                  <div>
                    <p className="text-gray-400 text-sm">Reason</p>
                    <p className="text-white">{selectedRequest.reason || 'No reason provided'}</p>
                  </div>

                  {selectedRequest.bank_details && (
                    <div>
                      <p className="text-gray-400 text-sm">Bank Details</p>
                      <div className="bg-gray-800/50 p-4 rounded-lg mt-1">
                        <p className="text-white">Bank: {selectedRequest.bank_details.bank_name}</p>
                        <p className="text-white">Account: {selectedRequest.bank_details.account_number}</p>
                        <p className="text-white">Holder: {selectedRequest.bank_details.account_holder}</p>
                        <p className="text-white">IFSC: {selectedRequest.bank_details.ifsc}</p>
                      </div>
                    </div>
                  )}

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-gray-400 text-sm">Request Date</p>
                      <p className="text-white">{new Date(selectedRequest.created_at).toLocaleString()}</p>
                    </div>
                    {selectedRequest.processed_at && (
                      <div>
                        <p className="text-gray-400 text-sm">Processed Date</p>
                        <p className="text-white">{new Date(selectedRequest.processed_at).toLocaleString()}</p>
                      </div>
                    )}
                  </div>

                  {selectedRequest.completed_at && (
                    <div>
                      <p className="text-gray-400 text-sm">Completed Date</p>
                      <p className="text-white">{new Date(selectedRequest.completed_at).toLocaleString()}</p>
                    </div>
                  )}

                  {selectedRequest.transaction_id && (
                    <div>
                      <p className="text-gray-400 text-sm">Transaction ID</p>
                      <p className="text-white font-mono">{selectedRequest.transaction_id}</p>
                    </div>
                  )}

                  {selectedRequest.admin_notes && (
                    <div>
                      <p className="text-gray-400 text-sm">Admin Notes</p>
                      <p className="text-white">{selectedRequest.admin_notes}</p>
                    </div>
                  )}

                  {selectedRequest.rejection_reason && (
                    <div className="bg-red-500/10 border border-red-500/20 rounded-lg p-4">
                      <p className="text-red-400 text-sm font-medium">Rejection Reason</p>
                      <p className="text-white mt-1">{selectedRequest.rejection_reason}</p>
                    </div>
                  )}
                </div>

                <div className="flex justify-end mt-6">
                  <UnifiedButton
                    variant="secondary"
                    onClick={closeDetailsModal}
                  >
                    Close
                  </UnifiedButton>
                </div>
              </div>
            </UnifiedCard>
          </div>
        )}
      </UnifiedBackground>
    </>
  );
}

export default AdminWithdrawals;
